.profile{
    width: 80%;
    display: flex;
    justify-content: space-around;
    background-color: rgb(199, 198, 190);
    height: 500px;
    left: 50%;
    margin: 10px auto;
}
.profile-img{
    position: relative;
    height: 100%;
}
img{
    position: absolute;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    right: -220px;
    top: 100px;
}

.profile-img::after{
    position: absolute;
    content: "";
    width: 2px;
    height: 100%;
    background-color: black;
    left: 310px;
}

.profile-data{
    width: 40%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: space-evenly;
}
.profile-data a{
    text-decoration: none;
    color: inherit;
    width: 100px;
    height: 40px;
    background-color: black;
    color: white;
}
