.all-repos h2{
    text-align: center;
}
ul{
    width: 98%;
    background-color: rgb(180, 180, 179);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    list-style-type: none;
    margin: 10px auto;
}
.repos{
    position: relative;
    width: 400px;
    height: 200px;
    background-color: rgb(236, 236, 236); 
    margin: 10px auto;  
    color: rgb(0, 0, 0);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow:2px 5px 6px 1px rgba(0, 0, 0, 0.6);
}

.repos h3{
    position: absolute;
    top: 40px;
}

li{
    position: absolute;
    top: 70px;
    font-size: 23px;
    font-weight: 700;
}

button{
    position: absolute;
    width: 100px;
    height: 30px;
    top: 120px;
    border-radius: 10px;
    text-align: center;
    border: none;
    box-shadow:2px 4px 4px 1px rgba(0, 0, 0, 0.2);
}

button:hover{
    background-color: white;
}

a{
    text-decoration: none;
    color: rgb(0, 0, 0);;
}